import React, {useState, useEffect} from "react";
import axios from 'axios';

axios.defaults.baseURL = 'http://localhost:8080';

export function LoginContainer() {

    let [isLogin, loginStatus] = useState(false);
    let [userName, setName] = useState('')
    let [userSession, setSession] = useState('')
    let [datosCorrectos, setBadLogin] = useState(false)


    async function login(){

        setBadLogin(false)

        axios.get('/login/?user=martin@garmin.com.ar&pass=sarasasa')
        .then(res => {
            setName(res.data.mensaje)
            setSession(res.data.idSession)
            loginStatus(true)
        })
        .catch(err => {
                setBadLogin(true)
        })
    }

    function logout(){
        axios.get('/logout')
        .then(res => {
            loginStatus(false)
        })
        .catch(console.log)
    }


    return (
        <React.Fragment>
            {isLogin ? 
            
            <React.Fragment>
                <div className="m-4 p-4 bg-primary">
                    <h1>{userName}</h1>
                    <h2>Su id de sesion es: {userSession}</h2>
                    <button onClick={()=>logout()} className="mt-3">Desloguear</button>
                </div>
            </React.Fragment>

            :
            <React.Fragment>     
                <React.Fragment>
                <form className="m-4 p-4 bg-primary">
                    <div className="form-group">
                        <label>Usuario</label>
                        <input type="email" className="form-control" id="loginUser" aria-describedby="emailHelp" placeholder="ingresa usuario" />
                    </div>
                    <div className="form-group">
                        <label>Password</label>
                        <input type="password" className="form-control" id="loginPassword" placeholder="Password" />
                    </div>
                </form>
                    <button onClick={()=>login()}className="btn btn-primary">Ingresar</button>
                <React.Fragment>
                {!datosCorrectos ?
                <div></div>
                :
                <div style={{color: "red"}}>Datos Incorrectos</div>
                }
                </React.Fragment>
                </React.Fragment>
            </React.Fragment>
            }
        </React.Fragment>
    );
 
}
